// Farben
[data-dark="false"] {
  --primary: #6366F1; /* Soft Violettblau */
  --secondary: #A78BFA; /* Lila Akzent */
  --text: #111827;
  --background: #F9FAFB;
  --background-alt: #E5E7EB;
  --light-gray: #D1D5DB;
  --gray: #6B7280;
  --dark-gray: #374151;
  --overlay: #00000020;
}
[data-dark="true"] {
  --primary: #6366F1;
  --secondary: #4338CA;
  --text: #F3F4F6;
  --background: #0B0B12;
  --background-alt: #111827;
  --light-gray: #1F2937;
  --gray: #6B7280;
  --dark-gray: #9CA3AF;
  --overlay: #ffffff10;
}

:root {
  --title: "Inter", sans-serif;
  --heading: "Inter", sans-serif;
  --body: "Inter", sans-serif;

  --large: 1.3rem;
  --xl: 1.6rem;
  --xxl: 1.8rem;

  --thin: 300;
  --regular: 400;
  --semi-bold: 600;
  --bold: 700;

  --spacing: 1.8;
  --compact: 1.4;

  --rounded: 8px;
  --shadow: 0 4px 20px 0 var(--overlay);
  --transition: 0.3s ease-in-out;
}
